@import url(https://fonts.googleapis.com/css?family=Raleway:400);
*{margin:0;padding:0;list-style:none;vertical-align:baseline}body{background-color:#333;color:#fff;font-family:"Raleway", sans-serif;font-weight:400;display:flex;flex-direction:column;justify-content:center;align-items:center;height:100vh;transition:all 0.5s ease-in-out}.quote-box{border-radius:3px;position:relative;width:450px;padding:30px 40px;display:flex;flex-direction:column;background-color:#fff;transition:all 0.5s ease-in-out}.quote-box .quote-text{width:450px;height:auto;clear:both;font-weight:500;font-size:1.4em}.quote-box .quote-text i{font-size:1em;margin-right:0.4em}.quote-box .quote-author{width:450px;height:auto;clear:both;padding-top:20px;font-size:1em;text-align:right}.quote-box .buttons{width:450px;margin:auto;display:block}.quote-box .buttons .button{height:38px;border:none;border-radius:3px;color:#fff;background-color:#333;outline:none;font-size:0.85em;padding:8px 18px 6px 18px;margin-top:30px;opacity:1;transition:all 0.5s ease-in-out;cursor:pointer}.quote-box .buttons .button:hover{opacity:0.5}.quote-box .buttons .button#tweet-quote{float:left;padding:0px;padding-top:8px;text-align:center;font-size:1.2em;margin-right:5px;height:30px;width:40px}.quote-box .buttons .button#new-quote{float:right}.footer{width:450px;text-align:center;margin:15px auto 30px auto;font-size:0.8em;color:#fff}.footer a{font-weight:500;text-decoration:none;color:#fff}

